import * as React from 'react';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';

export default function PaymentForm({ fields, onChange }) {
  function handleChange(event) {
    let newFields;

    if (event.target.name === 'iAgree') {
      newFields = { ...fields, iAgree: fields.iAgree === "no" ? "yes" : "no" };
    } else {
      newFields = { ...fields, [event.target.id]: event.target.value };
    }

    newFields.valid = !!newFields.cardName 
                      && !!newFields.cardNumber 
                      && !!newFields.expDate
                      && !!newFields.cvv                  
                      && newFields.iAgree === "yes";
                                        
    onChange(newFields);
  }

  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
        Payment method
      </Typography>
      <Grid container spacing={3}>
        <Grid item xs={12} md={6}>
          <TextField
            required
            id="cardName"
            label="Name on card"
            fullWidth
            autoFocus
            autoComplete="cc-name"
            variant="standard"
            value={fields.cardName}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12} md={6}>
          <TextField
            required
            id="cardNumber"
            label="Card number"
            fullWidth
            autoComplete="cc-number"
            variant="standard"
            value={fields.cardNumber}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12} md={6}>
          <TextField
            required
            id="expDate"
            label="Expiry date"
            fullWidth
            autoComplete="cc-exp"
            variant="standard"
            value={fields.expDate}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12} md={6}>
          <TextField
            required
            id="cvv"
            label="CVV"
            helperText="Last three digits on signature strip"
            fullWidth
            autoComplete="cc-csc"
            variant="standard"
            value={fields.cvv}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12}>
          <FormControlLabel
            control={<Checkbox id="iAgree" color="secondary" name="iAgree" checked={fields.iAgree==="yes"} value={fields.iAgree} onChange={handleChange}/>}
            label="I agree to pay a reservation fee of $1"
          />
        </Grid>
      </Grid>
    </React.Fragment>
  );
}